<template>
  <div class>
    <h1>{{ id ? "" : "" }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="" style="width: 500px">
        <el-input v-model="model.title"></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="model.categories">
          <el-option
            v-for="item of categories"
            :label="item.name"
            :value="item._id"
            :key="item._id"
          ></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="" style="width: 500px">
        <el-input v-model="model.href"></el-input>
      </el-form-item>

      <el-form-item label="banner" style="width: 500px">
        <el-input v-model="model.banner"></el-input>
      </el-form-item>

      <el-form-item label="" style="width: 500px">
        <el-input v-model="model.date"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    // id
    id: {}
  },
  data() {
    return {
      model: {},
      categories: []
    }
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        console.log("");
        res = await this.$http.put(`rest/strategies/${this.id}`, this.model); // 
      } else {
        res = await this.$http.post("rest/strategies", this.model); // 
      }

      console.log(res.data);

      this.$router.push("/strategies/list");
      this.$message({
        // 
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/strategies/${this.id}`); // 
      this.model = res.data;
      // console.log(res.data);
    },
    async fetchCategories() {
      const res = await this.$http.get(`rest/categories`); // 
      this.categories = res.data;
      console.log(this.categories);
    },
  },
  created() {
    this.id && this.fetch(); // id this.fetch()
    this.fetchCategories()
  }
};
</script>
