<template>
  <div class>
    <h1>{{ id ? "" : "" }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-tabs value="skills" type="border-card">
        <el-tab-pane label="">
          <el-form-item label="">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <!-- $http.defauts.baseURL + '/upload'  =>  http://localhost:3000/admin/api/upload    actionmodel.icon -->
            <el-upload
              class="item-uploader"
              :action="uploadUrl"
              :headers="getAuthHeaders()"
              :show-file-list="false"
              :on-success="res => $set(item, 'icon', res.avatar)"
            >
              <!--  +  -->
              <img v-if="model.avatar" :src="model.avatar" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="getAuthHeaders()"
              :show-file-list="false"
              :on-success="res => $set(model, 'banner', res.url)"
            >
              <!-- $set(item, 'icon', res.url) vue -->
              <!--  +  -->
              <img v-if="model.banner" :src="model.banner" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="model.categories" multiple>
              <el-option
                v-for="item of categories"
                :label="item.name"
                :value="item._id"
                :key="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top: 1em" :max="9" show-score v-model="model.scores.difficult"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top: 1em" :max="9" show-score v-model="model.scores.skills"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top: 1em" :max="9" show-score v-model="model.scores.attack"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top: 1em" :max="9" show-score v-model="model.scores.survive"></el-rate>
          </el-form-item>

          <el-form-item label="">
            <el-select v-model="model.items1" multiple>
              <el-option v-for="item of items" :label="item.name" :value="item._id" :key="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.items2" multiple>
              <el-option v-for="item of items" :label="item.name" :value="item._id" :key="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.usageTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.battleTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.teamTips"></el-input>
          </el-form-item>
        </el-tab-pane>

        <el-tab-pane label="" name="skills">
          <el-button size="small" @click="model.skills.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col :md="12" v-for="(item, i) in model.skills" :key="i">
              <el-form-item style="width: 400px" label="">
                <el-input v-model="item.name"></el-input>
              </el-form-item>

              <el-form-item style="width: 400px" label="">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item style="width: 400px" label="">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.tips" type="textarea"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-upload
                  class="avatar-uploader"
                  :action="uploadUrl"
                  :headers="getAuthHeaders()"
                  :show-file-list="false"
                  :on-success="res => $set(item, 'icon', res.url)"
                >
                  <!-- $set(item, 'icon', res.url) vue -->
                  <!--  +  -->
                  <img v-if="item.icon" :src="item.icon" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>

              <el-form-item>
                <el-button size="small" type="danger" @click="model.skills.splice(i,1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="partners">
          <el-button size="small" @click="model.partners.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col :md="12" v-for="(item, i) in model.partners" :key="i">
              <el-form-item label="">
                <el-select v-model="item.hero" filterable>
                  <el-option
                    v-for="hero in heroes"
                    :key="hero._id"
                    :value="hero._id"
                    :label="hero.name"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button size="small" type="danger" @click="model.partners.splice(i,1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="restrained">
          <el-button size="small" @click="model.restrained.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col :md="12" v-for="(item, i) in model.restrained" :key="i">
              <el-form-item label="">
                <el-select v-model="item.hero" filterable>
                  <el-option
                    v-for="hero in heroes"
                    :key="hero._id"
                    :value="hero._id"
                    :label="hero.name"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button size="small" type="danger" @click="model.restrained.splice(i,1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="restrain">
          <el-button size="small" @click="model.restrain.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col :md="12" v-for="(item, i) in model.restrain" :key="i">
              <el-form-item label="">
                <el-select v-model="item.hero" filterable>
                  <el-option
                    v-for="hero in heroes"
                    :key="hero._id"
                    :value="hero._id"
                    :label="hero.name"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button size="small" type="danger" @click="model.restrain.splice(i,1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>

        <el-tab-pane label="" name="moreTips">
          <el-button size="small" @click="model.moreTips.push({})">
            <i class="el-icon-plus"></i>
          </el-button>
          <el-row type="flex" style="flex-wrap: wrap">
            <el-col :md="12" v-for="(item, i) in model.moreTips" :key="i">
              <el-form-item label="">
                <el-input v-model="item.title"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-upload
                  class="avatar-uploader"
                  :action="uploadUrl"
                  :headers="getAuthHeaders()"
                  :show-file-list="false"
                  :on-success="res => $set(item, 'banner', res.url)"
                >
                  <img v-if="item.banner" :src="item.banner" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.href"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.count"></el-input>
              </el-form-item>

              <el-form-item label="">
                <el-input v-model="item.date"></el-input>
              </el-form-item>

              <el-form-item>
                <el-button size="small" type="danger" @click="model.moreTips.splice(i,1)"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>

      <el-form-item style="margin-top: 1rem">
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    // id
    id: {}
  },
  data() {
    return {
      model: {
        name: "",
        avatar: "",
        skills: [],
        heroes: [],
        partners: [],
        restrained: [],
        restrain: [],
        moreTips: [],
        scores: {
          difficult: 0
        }
      },
      categories: [],
      items: []
    };
  },
  methods: {
    async save() {
      let res;
      if (this.id) {
        console.log("");
        res = await this.$http.put(`rest/heroes/${this.id}`, this.model); // 
      } else {
        res = await this.$http.post("rest/heroes", this.model); // 
      }

      console.log(res.data);

      // this.$router.push("/heroes/list");
      this.$message({
        // 
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/heroes/${this.id}`); // 
      this.model = Object.assign({}, this.model, res.data); //  this.model  res.data this.model
      console.log(res.data);
    },
    async fetchCategories() {
      const res = await this.$http.get(`rest/categories`); // 
      this.categories = res.data;
      console.log(res.data);
    },
    async fetchItems() {
      const res = await this.$http.get(`rest/items`); // 
      this.items = res.data;
      console.log(res.data);
    },
    async fetchHeroes() {
      const res = await this.$http.get(`rest/heroes`); // 
      this.heroes = res.data;
    }
  },
  created() {
    this.fetchItems();
    this.fetchHeroes();
    this.fetchCategories();
    this.id && this.fetch(); // id this.fetch()
  }
};
</script>
