import axios from 'axios'
import Vue from 'vue'
import router from './router'

const http = axios.create({
  baseURL: process.env.VUE_APP_API_URL || 'admin/api', 
  // baseURL: 'http://localhost:3000/admin/api'
})


// 
http.interceptors.request.use(function (config) {
  // Do something before request is sent
  if(localStorage.token) {
    config.headers.Authorization = 'Bearer ' + localStorage.token //  token
  }
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});


//   npm axios
http.interceptors.response.use(res => { //  
  return res
}, err => { // 
  if (err.response.data.message) {
    Vue.prototype.$message({ // elementmessagemessage
      type: 'error',
      message: err.response.data.message // err.response.data.message message
    })

    if(err.response.status === 401) { // 401token 
      router.push('/login')
    }
  }

  return Promise.reject(err)
})

export default http