<template>
  <div class>
    <h1>{{ id ? "" : "" }}</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="" style="width: 500px">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <!-- actionmodel.icon -->
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()" 
          :show-file-list="false"
          :on-success="afterUpload"
        > <!-- getAuthHeaders() main.js, element  token
              uploadUrl main.js
           -->
          <!--  +  -->
          <img v-if="model.icon" :src="model.icon" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    // id
    id: {}
  },
  data() {
    return {
      model: {}
    }
  },
  methods: {
    afterUpload(res) {
      this.$set(this.model, 'icon', res.url) //  model  url , res.url
      // this.model.icon = res.url  
    },
    async save() {
      let res;
      if (this.id) {
        console.log("")
        res = await this.$http.put(`rest/items/${this.id}`, this.model) // 
      } else {
        res = await this.$http.post("rest/items", this.model) // 
      }

      console.log(res.data)

      this.$router.push("/items/list")
      this.$message({
        // 
        type: "success",
        message: ""
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/items/${this.id}`) // 
      this.model = res.data
      console.log(res.data)
    }
  },
  created() {
    this.id && this.fetch() // id this.fetch()
  }
};
</script>
